import Card from "../card/Card";
import Item from "./Item";
import ItemHabilidade from "./ItemHabilidade";

const Habilidade = () => {
    return (
        <section id="habilidade">
            <Card>
                <section id="habilidade__descricao">
                    <h3>Habilidades</h3>
                    <hr /><br />
                    <section className="row">
                        <ItemHabilidade title="Front-end">
                            <Item name="HTML5" />
                            <Item name="CSS" />
                            <Item name="React Js" />
                            <Item name="Wordpress" />
                        </ItemHabilidade>
                        <ItemHabilidade title="Back-end">
                            <Item name="JavaScript" />
                            <Item name="Express" />
                            <Item name="Node Js" />
                            <Item name="PHP" />
                            <Item name="Java" />
                        </ItemHabilidade>
                        <ItemHabilidade title="Ferramentas & Outros">
                            <Item name="Git" />
                            <Item name="MySQL" />
                            <Item name="PHPMyAdmin" />
                            <Item name="VS Code" />
                            <Item name="Next Js" />
                            <Item name="Rest API" />
                        </ItemHabilidade>
                    </section>
                </section>
            </Card>
        </section>
    )
}

export default Habilidade;